<!-- 本页面属于 巡查单详情的 委派状态 status === 20 || 30 -->
<template>
  <view class="task-detail padding-bottom-normal">
    <u-navbar
        isFixed
        :borderBottom="false"
        backIconName="arrow-leftward"
        titleSize="34"
        :customBack="back"
        :background="background"
        title="巡查单详情"></u-navbar>
    <view class="margin-normal bg-white padding-normal radius-normal" @click="foMaintProcess">
      <u-steps mode="number" :active-color="'#24A27C'" :list="numList" :current="current"></u-steps>
    </view>

    <view v-if="detail && !empty">
      <view>
        <nnkjc-detail-group>
          <nnkjc-detail-item
              detailTitle="工单编号"
              :detailContent="detail.seqNo"
              :align="'left'"
          >
          </nnkjc-detail-item>

          <nnkjc-detail-item
              detailTitle="创建时间"
              :detailContent="detail.createTime"
              :align="'left'"
          >
          </nnkjc-detail-item>

          <nnkjc-detail-item
              detailTitle="最近更新"
              :detailContent="detail.updateTime"
              :align="'left'"
          >
          </nnkjc-detail-item>

          <nnkjc-detail-item
              detailTitle="当前状态"
              :detailContent="detail.statusText"
              :align="'left'"
              :gap="false"
          >
          </nnkjc-detail-item>

        </nnkjc-detail-group>

        <nnkjc-detail-group>
          <nnkjc-detail-item
              detailTitle="巡查地点"
              :detailContent="'配电机房'"
              :align="'left'"
          >
          </nnkjc-detail-item>
          <nnkjc-detail-item
              detailTitle="巡查内容"
              :detailContent="detail.content"
              :align="'left'"
          >
          </nnkjc-detail-item>
          <nnkjc-detail-item
              detailTitle="抄表人"
              :detailContent="detail.patrolStaffName"
              :align="'left'"
              :gap="false"
          >
          </nnkjc-detail-item>
        </nnkjc-detail-group>
      </view>

      <!--      确认接单-->
      <view>
        <view v-show="showBtn1" class="save-tasks-inner-wrapper margin-normal radius-normal">
          <u-button ripple type="success" :loading="loadingConfirm" @click="confirmReceive">确认接单</u-button>
        </view>

        <view v-show="showBtn2" class="save-tasks-inner-wrapper margin-normal radius-normal">
          <u-button ripple :loading="loadingRefuse" type="error" plain @click="showModal = true">
            <text style="color: #ff3534;">拒绝</text>
          </u-button>
        </view>
      </view>

      <view>
        <view v-show="showBtn3" class="save-tasks-inner-wrapper margin-normal radius-normal">
          <u-button ripple type="success" :loading="loading" @click="reDelegatePerson">重新委派</u-button>
        </view>
      </view>

      <view>
        <view v-show="showBtn4" class="save-tasks-inner-wrapper margin-normal radius-normal">
          <u-button ripple type="success" :loading="loading" @click="forMakeMaintRecord">填写巡查记录</u-button>
        </view>
      </view>

      <view>
<!--        <view v-show="showBtn5" class="save-tasks-inner-wrapper margin-normal">-->
<!--          <u-button class="plain anti-success" ripple :loading="loading" @click="transDelegatePerson">-->
<!--            <text style="color: #23a27b;">转派他人处理</text>-->
<!--          </u-button>-->
<!--        </view>-->
      </view>
    </view>

    <u-empty :show="empty" text="暂无任务详情" mode="history"></u-empty>

    <u-modal v-model="showModal" @confirm="$u.debounce(confirmRefuse, 500)" show-cancel-button
             content="确认拒绝该工单？"></u-modal>

    <!--    重新委派-->
    <u-select
        title="选择维修人"
        :default-value="personDefaultV"
        mode="single-column"
        v-model="showPerson"
        @confirm="personConfirm"
        @cancel="personCancel"
        :list="personList"></u-select>
    <u-toast ref="uToast"/>

    <!--    转派-->

<!--    <u-select-->
<!--        title="选择维修人"-->
<!--        :default-value="personDefaultV"-->
<!--        mode="single-column"-->
<!--        v-model="showPersonTrans"-->
<!--        @confirm="personConfirmTrans"-->
<!--        @cancel="personCancel"-->
<!--        :list="personList"></u-select>-->
<!--    <u-toast ref="uToast"/>-->
  </view>
</template>

<script>
  export default {
    name: "patrol-detail-delegated",
    data() {
      return {
        personIdIndexMap: {},
        showPerson: false,
        showPersonTrans: false,
        personDefaultV: [0],
        personList: [],
        showBtn1: false,  //  确认接单
        showBtn2: false,  //  拒单
        showBtn3: false,  //  重新委派
        showBtn4: false,  //  填写维修记录
        // showBtn5: false,  //  转单
        showModal: false,
        loading: false,
        loadingRefuse: false,
        loadingConfirm: false,
        empty: false,
        detail: {
          seqNo: "",
          createTime: "",
          updateTime: "",
          statusText: ""
        },
        background: {
          'background': '#f2f2f8'
        },
        current: 1,
        numList: [{
          name: '新建'
        }, {
          name: '委派'
        }, {
          name: '维修'
        }, {
          name: '检定'
        },],
        searchParams: {
          id: "",
          status: "",
          type: ""
        }
      }
    },
    computed: {
      PREMISS() {
        return this.$store.state.PREMISS
      }
    },
    onLoad(options) {
      this.searchParams.id = options.taskId
      this.searchParams.status = options.status
      this.searchParams.type = options.type
    },
    onReady() {
      this.getDetail();
    },
    onShow() {
      this.getPersons();
    },
    methods: {
      getPersons() {
        this.$u
            .post("/sys/staff/getStaffListForWexiu")
            .then(res => {
              if (res.data) {
                this.personList = res.data.map((person, index) => {
                  this.personIdIndexMap[person.id] = index;
                  return {
                    value: person.id,
                    label: person.staffName
                  }
                })
              }
            })
            .catch(err => {
            });
      },
      //  重新委派
      personConfirm(v) {
        const user = uni.getStorageSync("user");
        if (user && user.staffId === v[0].value) {
          this.$refs.uToast.show({
            title: "不能派单给自己",
            type: "warning"
          });
          return;
        }
        this.personDefaultV = [this.personIdIndexMap[v[0].value] || 0];
        this.$u.post("maint/saveSend", {
          taskId: this.searchParams.id || this.detail.id,
          type: this.searchParams.type,
          maintStaffIds: v[0].value
        }).then(res => {
          if (res.code === 200) {
            this.$store.commit("freshTaskFunc", true);
            this.$refs.uToast.show({
              title: res.message,
              type: "success"
            });
            setTimeout(() => {
              uni.reLaunch({
                url: "/pages/home/index"
              }, 500)
            })
          }
        }).catch(e => {

        }).finally(_ => {
          this.showPerson = false;
        })
      },
      //  转派
      // personConfirmTrans(v) {
      //   const user = uni.getStorageSync("user");
      //   if (user && user.staffId === v[0].value) {
      //     this.$refs.uToast.show({
      //       title: "不能转单给自己",
      //       type: "warning"
      //     });
      //     return;
      //   }
      //   this.personDefaultV = [this.personIdIndexMap[v[0].value] || 0];
      //   this.$u.post("maint/transSend", {
      //     taskId: this.searchParams.id || this.detail.id,
      //     sendId: this.detail.sendId,
      //     maintStaffId: v[0].value
      //   }).then(res => {
      //     if (res.code === 200) {
      //       this.$store.commit("freshTaskFunc", true);
      //       this.$refs.uToast.show({
      //         title: res.message,
      //         type: "success"
      //       });
      //       setTimeout(() => {
      //         uni.navigateTo({
      //           url: "/pages/home/index"
      //         }, 500)
      //       })
      //     }
      //   }).catch(e => {
      //
      //   }).finally(_ => {
      //     this.showPerson = false;
      //   })
      // },
      personCancel() {
        this.showPerson = false;
        this.showPersonTrans = false;
      },
      back() {
        uni.navigateBack({
          delta: 1
        })
      },
      //  确认接单
      confirmReceive() {
        this.loadingConfirm = true;
        this.$u
            .post("maint/recSend", {
              taskId: this.searchParams.id,
              sendId: this.detail.sendId,
              type: this.searchParams.type
            }).then(res => {
          if (res.code === 200) {
            this.$refs.uToast.show({
              title: "接单成功",
              type: "success"
            });
            setTimeout(() => {
              uni.reLaunch({
                url: "/pages/home/index"
              });
            }, 500)
          }
        }).catch(e => {

        }).finally(_ => {
          this.loadingConfirm = false;
        })
      },
      //  拒单操作
      //  工单拒单（仅限维修工单和巡查工单）
      confirmRefuse() {
        if (this.loadingRefuse) {
          return
        }
        if (this.searchParams.type === "xuncha") {
          this.loadingRefuse = true;
          this.$u
              .post("/maint/refuseSend", {
                taskId: this.detail.id,
                sendId: this.detail.sendId,
                type: this.searchParams.type
              })
              .then(res => {
                if (res.code === 200) {
                  this.$store.commit("freshTaskFunc", true);
                  this.$refs.uToast.show({
                    title: res.message,
                    type: "success"
                  });
                  setTimeout(() => {
                    uni.reLaunch({
                      url: "/pages/home/index"
                    }, 500)
                  })
                }
              })
              .catch(err => {
              }).finally(_ => {
            this.loadingRefuse = false;
          })
        } else {
          this.$refs.uToast.show({
            title: "仅能拒绝维修工单以及巡查工单",
            type: "warning"
          });
        }
      },
      foMaintProcess() {
        const params = this.$u.queryParams({
          taskId: this.searchParams.id,
        })

        uni.navigateTo({
          url: "/pages/maint-process/index" + params
        })
      },
      getDetail() {
        this.$u
            .post("/maint/getPatrolDetail", this.searchParams)
            .then(res => {
              if (res.data) {
                const user = uni.getStorageSync("user")
                const premissObj = uni.getStorageSync("premissObj")
                this.detail = res.data;
                if (this.detail) {

                  //  用户的 staffid 与 表单的抄表人一致
                  const rightStaff = user && user.staffId === this.detail.patrolStaffId

                  const rightStatus1 = this.searchParams.status == "20";
                  const rightStatus2 = this.searchParams.status == "30";

                  //  状态是 20 && 有接单的权限 && staffID 一致 (见详情)
                  this.showBtn1 = !!(rightStatus1 && premissObj && premissObj[this.PREMISS.reveive] && rightStaff)

                  //  状态是 20 && 有拒绝的权限 && staffID 一致 (见详情)
                  this.showBtn2 = !!(rightStatus1 && premissObj && premissObj[this.PREMISS.refuse] && rightStaff)

                  //  状态是 20 && 有重新委派的权限 && staffID 一致 (见详情)
                  this.showBtn3 = !!(rightStatus1 && premissObj && premissObj[this.PREMISS.reDelegate])

                  //  状态是 30 && 有填写巡查记录的权限 && staffID 一致 (见详情)
                  this.showBtn4 = !!(rightStatus2 && premissObj && premissObj[this.PREMISS.saveMaintRecord] && rightStaff)

                  //  状态是 30 && 有转派的权限 && staffID 一致 (见详情)
                  // this.showBtn5 = !!(rightStatus2 && premissObj && premissObj[this.PREMISS.trans] && rightStaff)

                  const res = [{
                    "表单状态": this.detail.status,
                    "抄表人 id": this.detail.patrolStaffId,
                    "用户 staffId": user.staffId,
                    "表单类型": this.detail.type,
                    "修改pm": premissObj[this.PREMISS.modifyreason],
                    "取消pm": premissObj[this.PREMISS.delitem],
                    "委派pm": premissObj[this.PREMISS.delegate],
                    "重新委派pm": premissObj[this.PREMISS.reDelegate],
                    "接单pm": premissObj[this.PREMISS.reveive],
                    "拒单pm": premissObj[this.PREMISS.refuse],
                    "转单pm": premissObj[this.PREMISS.trans],
                    "填写维修记录pm": premissObj[this.PREMISS.saveMaintRecord],
                  }];

                  console.table(res);
                }
              } else {
                this.empty = true;
              }
            })
            .catch(err => {
            });
      },
      reDelegatePerson() {
        this.showPerson = true;
      },
      // transDelegatePerson() {
      //   this.showPersonTrans = true;
      // },
      forMakeMaintRecord() {
        const params = this.$u.queryParams({
          taskId: this.detail.id,
          sendId: this.detail.sendId
        });

        uni.navigateTo({
          url: "/pages/make-patrol-record/index" + params
        })
      }
    }
  }
</script>

<style scoped>
  .u-btn--error--plain {
    background-color: #f2f2f8 !important;
  }
</style>